import { Component, Host, Listen, State, h, Element, Event, EventEmitter, Prop } from '@stencil/core';
import { TAuthNavigation } from './auth.types';
import { AuthService } from '@/services/api/auth.service';
import app_store from '@/stores/app.store';
import axios from 'axios';

@Component({
  tag: 'ir-auth',
  styleUrl: 'ir-auth.css',
  shadow: false,
})
export class IrAuth {
  @Element() el: HTMLElement;
  @Prop() enableSignUp: boolean = true;
  @State() authState: TAuthNavigation = 'login';
  @State() animationDirection: string = '';

  @State() signedIn: boolean = false;

  @Event() closeDialog: EventEmitter<null>;
  @Event() authFinish: EventEmitter<{ state: 'success' | 'failed'; token: string }>;

  // @Event() onSignIn: EventEmitter;
  // @Event() onSignOut: EventEmitter;

  private authService = new AuthService();
  googleButton: HTMLDivElement;
  // private signInDiv: HTMLDivElement;
  //private googleTrigger: HTMLDivElement;

  componentWillLoad() {
    this.authService.setToken(app_store.app_data.token);
    this.loadGoogleSignInScript();
    window.fbAsyncInit = () => {
      FB.init({
        appId: '1630011277802654',
        xfbml: true,
        version: 'v19.0',
      });

      FB.AppEvents.logPageView();
    };

    (function (d, s, id) {
      var js,
        fjs = d.getElementsByTagName(s)[0];
      if (d.getElementById(id)) {
        return;
      }
      js = d.createElement(s);
      js.id = id;
      js.src = 'https://connect.facebook.net/en_US/sdk.js';
      fjs.parentNode.insertBefore(js, fjs);
    })(document, 'script', 'facebook-jssdk');
  }
  loadGoogleSignInScript() {
    const script = document.createElement('script');
    script.src = 'https://accounts.google.com/gsi/client';
    script.async = true;
    script.defer = true;
    script.onload = () => this.initializeGoogleSignIn();
    document.head.appendChild(script);
  }

  initializeGoogleSignIn() {
    // Initialization logic here
    window.google.accounts.id.initialize({
      client_id: '1035240403483-60urt17notg4vmvjbq739p0soqup0o87.apps.googleusercontent.com',
      callback: this.handleCredentialResponse,
      auto_select: true,
      itp_support: true,
      ux_mode: true,
      context: 'popup',
    });
    window.google.accounts.id.renderButton(document.getElementById('buttonDiv'), { text: 'continue_with', theme: 'outline', size: 'large', width: '240px' });
  }

  async handleCredentialResponse(response) {
    console.log(response);
    const { data } = await axios.post(
      'https://gateway.igloorooms.com/IR/Exposed_Guest_SignIn?Ticket=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJleHAiOjE3MTY0NzAzMjYsIkNMQUlNLTAxIjoiZE1TL0hDV21NdW89IiwiQ0xBSU0tMDIiOiI5UStMQm93VTl6az0iLCJDTEFJTS0wMyI6Ilp3Tys5azJoTzUwPSIsIkNMQUlNLTA0IjoiQUVxVnRCMm1kWTg9IiwiQ0xBSU0tMDUiOiJFQTEzejA3ejBUcWRkM2gwNElyYThITHBnTWQvOWV5aSJ9.9hKPGjcvmTuRMAOE8BmXUY1NT3DDqSYDKgEokUV2N6U',
      { google_token: response.credential },
    );
    console.log(data);
  }

  async loginWithFacebook() {
    FB.login(response => {
      if (response.authResponse) {
        console.log('Welcome!  Fetching your information.... ');
        FB.api('/me', response => {
          console.log('Good to see you, ' + response.name + '.', response);
        });
      } else {
        console.log('User cancelled login or did not fully authorize.');
      }
    });
    FB.getLoginStatus(response => {
      console.log('login status', response);
    });
  }

  @Listen('navigate')
  handleNavigation(e: CustomEvent) {
    e.stopImmediatePropagation();
    e.stopPropagation();

    if (this.authState === 'login' && e.detail !== 'login') {
      this.animationDirection = 'slide-right';
    } else if (this.authState !== 'login' && e.detail === 'login') {
      this.animationDirection = 'slide-left';
    }

    this.authState = e.detail;
  }

  signUp(params: { email?: string; password?: string; first_name?: string; last_name?: string }) {
    try {
      const res = this.authService.signUp(params);
      console.log(res);
    } catch (error) {
      console.log(error);
    }
  }
  onSignIn = googleUser => {
    var profile = googleUser.getBasicProfile();
    console.log(profile);
    // Access profile information (e.g., profile.getId(), profile.getName(), etc.)
  };

  render() {
    return (
      <Host>
        <div class={`auth-container ${this.animationDirection} p-4 sm:p-6`}>
          {/* <div id="buttonDiv" ref={el => (this.googleTrigger = el)}></div> */}

          {this.authState === 'login' ? (
            <ir-signin
              // onAuthFinish={e => {
              //   console.log(e.detail);
              //   this.authFinish.emit(e.detail);
              // }}
              enableSignUp={this.enableSignUp}
              onSignIn={e => {
                e.stopImmediatePropagation();
                e.stopPropagation();
                if (e.detail.trigger === 'fb') {
                  return this.loginWithFacebook();
                } else if (e.detail.trigger === 'google') {
                  return;
                }
              }}
            ></ir-signin>
          ) : (
            <ir-signup
              onSignUp={e => {
                e.stopImmediatePropagation();
                e.stopPropagation();
                if (e.detail.trigger === 'be') {
                  return this.signUp(e.detail.params);
                } else if (e.detail.trigger === 'fb') {
                  return this.loginWithFacebook();
                }
                return;
              }}
            ></ir-signup>
          )}
          <div class="flex flex-col">
            <div class="flex w-full items-center justify-center">
              <div id="buttonDiv"></div>
            </div>

            <ir-social-button class="w-full outline" label="Continue with Facebook">
              <svg slot="icon" width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                <g clip-path="url(#clip0_1256_132001)">
                  <path
                    d="M24 12C24 5.37258 18.6274 0 12 0C5.37258 0 0 5.37258 0 12C0 17.9895 4.3882 22.954 10.125 23.8542V15.4688H7.07812V12H10.125V9.35625C10.125 6.34875 11.9166 4.6875 14.6576 4.6875C15.9701 4.6875 17.3438 4.92188 17.3438 4.92188V7.875H15.8306C14.34 7.875 13.875 8.80008 13.875 9.75V12H17.2031L16.6711 15.4688H13.875V23.8542C19.6118 22.954 24 17.9895 24 12Z"
                    fill="#1877F2"
                  />
                  <path
                    d="M16.6711 15.4688L17.2031 12H13.875V9.75C13.875 8.80102 14.34 7.875 15.8306 7.875H17.3438V4.92188C17.3438 4.92188 15.9705 4.6875 14.6576 4.6875C11.9166 4.6875 10.125 6.34875 10.125 9.35625V12H7.07812V15.4688H10.125V23.8542C11.3674 24.0486 12.6326 24.0486 13.875 23.8542V15.4688H16.6711Z"
                    fill="white"
                  />
                </g>
                <defs>
                  <clipPath id="clip0_1256_132001">
                    <rect width="24" height="24" fill="white" />
                  </clipPath>
                </defs>
              </svg>
            </ir-social-button>
          </div>

          {/* <div class="g-signin2" data-onsuccess="onSignIn"></div>
          <div
            id="g_id_onload"
            data-client_id="http://1035240403483-60urt17notg4vmvjbq739p0soqup0o87.apps.googleusercontent.com"
            data-context="use"
            data-ux_mode="popup"
            data-callback="handleCallback"
            data-auto_prompt="false"
          ></div>

          <div class="g_id_signin" data-type="standard" data-shape="rectangular" data-theme="outline" data-text="signin_with" data-size="large" data-logo_alignment="left"></div> */}
        </div>
      </Host>
    );
  }
}
